@font-face {
  font-family: "pub";
  src: local('pub'), url(../src/fonts/PublicSans-Regular.woff) format('woff');
}

body {
  margin: 0;
  padding: 0;
  font-family: "pub", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
